//首页时间筛选下拉框
.date-select {
  padding: 0 10px;
  line-height: 40px;
  cursor: pointer;
  &.selected {
    color: #50CDD2;
  }
  &:hover {
    background: #f0f0f0;
  }
}

.date-box {
  position: relative;
  margin: 20px 10px 10px;
  padding: 10px;
  background: #f5f5f5;
  &:before {
    content: "";
    display: inline-block;
    position: absolute;
    top: -10px;
    left: 0;
    width: 100%;
    height: 1px;
    background: #d4d4d4;
  }
}

.date-btn-box {
  margin: 10px 0;
  text-align: right;
}

//图表
.charts-warp {
  @include clearfix;
}

$charts-rW: 240px;
.charts-right {
  float: right;
  padding: 0 10px;
  width: $charts-rW;
  border: 1px solid #e9e9e9;
  border-radius: 10px;
}

.charts-li {
  padding: 15px 0;
  border-top: 1px solid #e9e9e9;
  &:first-child {
    border-top: none;
  }
  .charts {
    height: 221px;
  }
}

.charts-t {
  text-align: center;
  font-size: 16px;
}

.charts-box {
  margin-right: $charts-rW + 20;

}

.charts-main {
  padding: 15px 0;
  margin-top: 20px;
  border: 1px solid #e9e9e9;
  border-radius: 10px;
  &:first-child {
    margin-top: 0;
  }
}

.charts-t-box {
  .right {
    float: right;
    @include clearfix;
  }
}

.count-list {
  display: none;
  &.active {
    display: block;
  }
}

.count-li {
  float: left;
  position: relative;
  padding-left: 50px;
  margin-right: 15px;
  min-width: 125px;
  .icon-count-num,
  .icon-count-up {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 5px;
    @include icon;
    &:after {
      font-size: 40px;
    }
  }
  .icon-count-num:after {
    content: "\e629";
    color: #5ab1ef;
  }
  .icon-count-up:after {
    content: "\e62b";
    color: #2ec7c9;
  }
  label {
    display: block;
    line-height: 20px;
  }
  .count-num {
    font-size: 16px;
    font-weight: bold;
  }
}

.charts-btn-box {
  margin-right: 297px;
  padding-left: 10px;
}

a.charts-btn {
  display: inline-block;
  padding: 0 13px;
  margin-left: 8px;
  line-height: 40px;
  text-decoration: none;
  color: #383838;
  border: 1px solid #50d2c2;
  border-radius: 20px;
  &.active {
    background: #50d2c2;
    @include backgroundChang(left, #50cdd2, #50d2c2);
    color: #fff;
  }
}

.charts-big {
  height: 350px;
}

//图例
.legend {
  float: left;
  position: relative;
  padding-left: 28px;
  margin-right: 15px;
  line-height: 40px;
  i {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 10px;
    width: 20px;
    height: 20px;
    border-radius: 3px;
  }
  label {
    display: inline-block;
  }
}

$legendBg: #2ec7c9 #ffb980 #d87a80;
@for $i from 1 through 3 {
  .legend#{$i} {
    background: nth($legendBg, $i);
  }
}


